import React from 'react';

class PatientInfo extends React.Component {

    render() {
        console.log("patient Info ", this.props.patient);
        return <div className="container patient-info">
                    <table className="table">
                        <thead>
                            <tr className="row">
                                {Object.keys(this.props.patient).map( (value) => (<th className="col">{value}</th>) )}
                            </tr>
                        </thead>
                        <tbody>
                            <tr className="row">
                                {Object.values(this.props.patient).map( (value) => (<td className="col">{ Array.isArray(value) ? value.join(", ") : value.toString()}</td>))}
                            </tr>
                        </tbody>
                    </table>
                </div>;
    }
}

export default PatientInfo;
